/*-------------------------------*/
/*           Wrappers            */
/*-------------------------------*/
.nav li {
  float: left;
  }
#wrapper {
    padding-left: 0;
    transition: all 0.5s ease;
}

#wrapper.toggled {
    padding-right: 220px;
}

#sidebar-wrapper {
    z-index: 1000;
    top: 0;
    right: 220px;
    /*left: 220px;*/
    width: 0;
    height: 100%;
    margin-right: -220px;
    /*margin-left: -220px;*/
