}

#page-content-wrapper {
    width: 100%;
}

#wrapper.toggled #page-content-wrapper {
    position: absolute;
    margin-right: -220px;
}

/*-------------------------------*/
/*     Sidebar nav styles        */
/*-------------------------------*/
.navbar {
  padding: 0;
}

.sidebar-nav {
    position: absolute;
    top: 120px;
    width: 220px;
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: right;
    text-transform: uppercase;
}

.sidebar-nav li {
    position: relative; 
    line-height: 20px;
    display: inline-block;
    width: 100%;
}

.sidebar-nav li:before {
    content: '';
    position: absolute;
    top: 0;
    /*left: 0;*/
    right: 0;
    z-index: -1;
    height: 100%;
    width: 3px;
    background-color: #8ba48a;
    transition: width .2s ease-in;

}
.sidebar-nav li:hover{
  /*background: skyblue !important;*/
  border-radius: 10px;
  /*margin-left: 10px;
  margin-right: 10px;*/
}
.sidebar-nav li:hover:before,
.sidebar-nav li.open:hover:before {
    width: 100%;
    transition: width .2s ease-in;
           
}

.sidebar-nav li a {
    display: block;
    color: #fff;
    text-decoration: none;
    padding: 10px 0px 10px 30px; 
    font-family: 'Michroma', sans-serif;
    font-size: 11px;
    font-weight: 600;
    letter-spacing: 1px;
}

.sidebar-nav li a:hover,
.sidebar-nav li a:active,
.sidebar-nav li a:focus,
.sidebar-nav li.open a:hover,
.sidebar-nav li.open a:active,
.sidebar-nav li.open a:focus{
    text-decoration: none;
    /*background-color: transparent;
    color: #fff;*/
    border-color: #fff;
    border-bottom: 1px solid;
}
.sidebar-header {
    text-align: left;
    font-size: 20px;
    position: relative;
    width: 100%;
    display: inline-block;
}
.sidebar-brand {
    height: 65px;
